.productsContainer{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3,0fr);
    grid-row-gap: 20px;
    grid-column-gap: 75px;
    justify-items: center;
    justify-content: center;
}
.products{
    width:  220px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 15px;
   
    padding: 10px;
    -webkit-box-shadow: -2px 0px 20px -5px rgba(0,0,0,0.75);
    -moz-box-shadow: -2px 0px 20px -5px rgba(0,0,0,0.75);
    box-shadow: -2px 0px 20px -5px rgba(0,0,0,0.75);

    img{
        width: 100%;
    }

    button{
        border: none;
        border-radius: 4px;
        padding: 8px 25px;
        background-color: #e4002ac7;
    }

    button:hover{
        background-color: #e4002ae5;
        transition: 0.4s;
    }
}