.home{
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    flex-direction: column;

    .info{
        width: 100%;
        background-color: #222222d3;

        .info_contacto{
            display: flex;
            flex-wrap: wrap;
        
            p{
                color: #fff;
                padding: 0 50px;
                text-align: center;
                margin: 5px auto;
                display: block;
            }
        }
    }

    .productos{
        color: #222;
        padding: 25px 0;
        font-size: 25px;
        font-weight: bold;
    }

    .menu{
        width: 100%;
        background-color: #e4002b;
        -webkit-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
        -moz-box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
        box-shadow: 0px 5px 10px 0px rgba(0,0,0,0.75);
    }

    nav{
        padding: 20px 30px;
    }

    li{
        text-decoration: none;
        list-style: none;
        display: inline;
        padding: 0 10px;
        text-transform: uppercase;
        font-weight: bold;
        color: #fff;
    }
    .active:hover{
        background-color: #f70f39;
        padding: 10px 10px;
        border-radius: 8px;
    }
}
