.cartContainer{
    width: 100vw;
    height: 50px;
    display: flex;
    justify-content: flex-end;

    .buttonCartContainer{
        //position: absolute;
        //margin-top: 10px;
        margin-right: 20px;
        cursor: pointer;
        z-index: 3;
    }

    svg{
        height: 25px;
        margin-right: 2px;
    }
    
    .buttonCart{
        background-color: #222222;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 999px;
        margin: 10px;
    }
    
    .productsNumber{
        height: 20px;
        width: 20px;
        border-radius: 999px;
        top: 0;
        right: 0;
        background-color: #fb3939;
        font-size: 12px;
        font-weight: bold;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;

        margin-top: -70px;
        margin-left: 10px;
    }

}

.cart{
    position: absolute;
    margin-top: 10xp;
    margin-right: 20px;
    border-radius: 30px;
    width: 400px;
    background-color: #222222;
    color: white;
}

h2{
    text-align: center;
    font-weight: bold;
    margin-top: 35px;
}

.cartVacio{
    text-align: center;
    margin-top: 5px;
    font-size: 14px;
}

.total{
    text-align: center;
    font-weight: bold;
    margin-top: 15px;
    padding-bottom: 35px;
}

.productsContainer{
    max-height: 200px;
    overflow-y: scroll;
    margin-top: 5px;
}

::-webkit-scrollbar{
    -webkit-appearance: none;
}

::-webkit-scrollbar{
    width: 3px;
}

::-webkit-scrollbar-thumb{
    background-color: #797979;
    border-radius: 20px;
}